import React from 'react';
import './TransactionInfoModal.scss';
import arrowLeft from './img/arrowLeft.svg';
import mail from './img/mail.svg';
import question from './img/question.svg';
import receipt from './img/receipt.svg';
import remainder from './img/remainder.svg';
import template from './img/template.svg';
import repeat from './img/repeat.svg';
import userStore from '../../store/UserStore';
import { observer } from 'mobx-react-lite';

const TransactionInfoModal = observer(({isShow, setShow, onClick, transaction}) => {
  return (
    <>
      { isShow &&
        <div className='transactionInfoModal'>
          <div className='transactionInfoModal__header'>
            <img src={arrowLeft} alt='arrowLeft' onClick={() => setShow(false)}/>
            <img src={transaction.icon} className='transactionInfoModal__icon' alt={`${transaction.icon}`} />
          </div>
          <div className='transactionInfoModal__info'>
            <p className='transactionInfoModal__title'>Nike store</p>
            <p className='transactionInfoModal__date'>18 september, 2021  17:34 pm</p>
            <p className='transactionInfoModal__amountTitle'>Amount</p>
            <p className={`transactionInfoModal__amount ${transaction.plus 
              ? 'transactionInfoModal__amount-plus' 
              : 'transactionInfoModal__amount-minus'}`}>
                {userStore.currency + transaction.sum}
            </p>
          </div>
          <div className='transactionInfoModal__tail'>
            <div className='transactionInfoModal__trans'>
              <img src={remainder} alt='remainder' />
              <div className='transactionInfoModal__transInfo'>
                <div>Remainder</div>
                <p className='transactionInfoModal__subtitle'>$2 455 679</p>
              </div>
            </div>
            <div className='transactionInfoModal__trans'>
              <img src={receipt} alt='receipt' />
              <div className='transactionInfoModal__transInfo'>
                <div>receipt</div>
                <p className='transactionInfoModal__subtitle'>HRPTKKSJKAIU-RRYU</p>
              </div>
            </div>
            <div className='transactionInfoModal__links'>
              <div className='transactionInfoModal__link'>
                <img src={repeat} alt='repeat' />
                <p className='transactionInfoModal__subtitle'>Repiat payment</p>
              </div>
              <div className='transactionInfoModal__link'>
                <img src={mail} alt='mail' />
                <p className='transactionInfoModal__subtitle'>Mail a receipt</p>
              </div>
              <div className='transactionInfoModal__link'>
                <img src={template} alt='template' />
                <p className='transactionInfoModal__subtitle'>Create template</p>
              </div>
              <div className='transactionInfoModal__link'>
                <img src={question} alt='question' />
                <p className='transactionInfoModal__subtitle'>Any questions?</p>
              </div>
            </div>
          </div>
        </div>
      }
    </>
  )
});

export default TransactionInfoModal;