import React from 'react';
import './DayTransactions.scss';

import topup from '../../img/transactions/topup.svg';
import withdraw from '../../img/transactions/withdraw.svg';
import send from '../../img/transactions/send.svg';
import Transaction from '../Transaction/Transaction';
import { getSum } from '../../utils/transactions';


const DayTransactions = ({date, transactionsByDate, currency = '$', isDetail=false}) => {
  const getTransactionInfo = (transaction) => {
    switch(transaction.type) {
      case "payment_fill":
        return {
          icon: topup,
          title: 'Ya zakinul',
          method: 'Top up card',
          plus: true,
          sum: transaction.value
        }
      case "payment_made":
        return {
          icon: send,
          title: 'TO',
          method: 'Transfer money',
          plus: false,
          sum: transaction.value
        }
      case "payment_received":
        return {
          icon: send,
          title: 'From',
          method: 'Top up card',
          plus: true,
          sum: transaction.value
        }
      case "payment_withdraw":
        return {
          icon: withdraw,
          title: 'Bank account',
          method: 'Withdraw',
          plus: false,
          sum: transaction.value
        }
      default:
        console.log("Out of RANGE");
    }
  }

  return (
    <>
      <div className='day-transactions'>
        <p className='day-transactions__date'>{date}</p>
        <p className={`${getSum(transactionsByDate) >= 0 ? 'day-transactions__sum-plus' : 'day-transactions__sum-minus' } day-transactions__sum`}>
          {currency + Math.abs(getSum(transactionsByDate))}
        </p>
      </div>
      {
        transactionsByDate.map((t, index) => 
          <Transaction key={index + new Date().getTime()} transaction={getTransactionInfo(t)} isDetail={isDetail}/>
        )
      }
    </>
  );
};

export default DayTransactions;