import React from 'react';
import repeat from '../../img/transactions/repeat.svg';
import arrowDown from '../../img/UI/arrowDown.svg';
import arrowUp from '../../img/UI/arrowUp.svg';
import userStore from '../../store/UserStore';
import TransactionInfo from '../TransactionInfo/TransactionInfo';
import './Transaction.scss';
import TransactionInfoModal from './../TransactionInfoModal/TransactionInfoModal';
import { useState } from 'react';
import { MOBILE } from './../../utils/consts';

const Transaction = ({transaction, isDetail = false}) => {
  const detailInfoRef = React.createRef();
  const [showModal, setShowModal] = useState(false);

  const detailInfo = (e) => {
    let arrow = null;
    if(e.target.src) arrow = e.target;
    else arrow = e.target.children[0];
    arrow.src = arrow.src.split('/').pop() === arrowDown.split('/').pop() ? arrowUp : arrowDown; // toggle arrow in UI
    detailInfoRef.current.classList.toggle('visible');
  }

  const openTransactionInfoModal = () => {
    if(window.innerWidth < MOBILE) setShowModal(true);
  }

  return (
    <>
      <div className='transaction' onClick={openTransactionInfoModal}>
        <img src={transaction.icon} alt="" />
        <div className='transaction__info'>
          <p className='transaction__title'>{transaction.title}</p>
          <p className='transaction__method'>{transaction.method}</p>
        </div>
        <div className='transaction__tail'>
          <p className={`transaction__sum ${transaction.plus ? 'transaction__sum-plus' : 'transaction__sum-minus'}`}>
            {userStore.currency + transaction.sum}
          </p>
          {isDetail &&
            <div className='transaction__detail'>
              <div className='detail__icon'>
                <img src={repeat} alt="repeat" />
              </div>
              <div className='detail__icon' onClick={detailInfo}>
                <img src={arrowDown} alt="arrow" />
              </div>
            </div>
          }
        </div>
      </div>
      <TransactionInfoModal isShow={showModal} setShow={setShowModal} transaction={transaction}/>
      {isDetail && <TransactionInfo ref={detailInfoRef}/> }
    </>
  );
};

export default Transaction;