import React, { useEffect, useState } from 'react';
import './Transactions.scss';

import DayTransactions from './../DayTransactions/DayTransactions';
import empty from '../../img/UI/empty.svg';
import { getDateName } from '../../utils/dates';

const Transactions = React.memo(({transactions, classes = '', isDetail = false, isResults = true}) => {
  const [transactionsByDates, setTransactionsByDates] = useState([]);

  useEffect(() => {
    let dates = [];
    transactions?.forEach(transaction => dates.push(transaction.date));
    const uniqDates = [...new Set(dates)].reverse();
    setTransactionsByDates(uniqDates.map(date => 
      transactions?.filter((transaction) => transaction.date === date).reverse()));
  }, [transactions]);

  return (
    <div className={`transactions ${classes}`}>
      {
        isResults 
          ?
            transactions !== null && transactions?.length !== 0 
            ?
              <>
                {
                  transactionsByDates.map((transactionsByDate) => 
                    <DayTransactions 
                      key={transactionsByDate[0].date} 
                      date={getDateName(transactionsByDate[0].date)}
                      transactionsByDate={transactionsByDate}
                      isDetail={isDetail}/>
                  )
                }
                <div className='transactions__top'></div>
              </>
            :
              <div className='empty'>
                <div className='empty__content'>
                  <img className='empty__icon' src={empty} alt="empty" />
                  <div className='empty__info'>You haven't made any transactions yet</div>          
                </div>
              </div>
          :
            <div className='empty'>
              <div className='empty__content empty__content-filtered'>
                <img className='empty__icon' src={empty} alt="empty" />
                <div className='empty__info empty__info-filtered'>Zero transactions on these filters</div>          
              </div>
            </div>
        
      }
    </div>
  );
});

export default Transactions;