import React from 'react';
import arrowRight from '../../img/UI/arrowRight.svg';
import checkmark from '../../img/UI/checkmark.svg';
import arrowDownward from '../../img/UI/arrowDownward.svg';
import email from '../../img/transactions/email.svg';
import './TransactionInfo.scss';

const TransactionInfo = React.forwardRef((props, ref) => {
  return (
    <div ref={ref} className='transaction-info'>
        <div className='transaction-info__container'>
          <div className='transaction-info__block'>
            <div className='block__title'>Sender</div>
            <div className='block__content'>
              <div className='block__title block__title-big'>Dora Sher</div>
              <div className='block__account'>223458856 | Account</div>
            </div>
            <div className='block__tail'>
              <div className='block__transfer'>
                <div className='block__title'>Amount</div>
                <div className='block__money'>$475.22</div>
              </div>
              <div className='block__transfer'>
                <div className='block__title'>Fee</div>
                <div className='block__money'>0.00</div>
              </div>
            </div>
          </div>
          <div className='transaction-info__arrow'>
            <img src={arrowRight} alt="arrowRight" />
          </div>
          <div className='transaction-info__block'>
            <div className='block__title'>recipient</div>
            <div className='block__content'>
              <div className='block__title block__title-big'>Esther Howard</div>
              <div className='block__account'>ester@ester.com</div>
            </div>
            <div className='block__tail'>
              <div className='block__transfer'>
                <div className='block__title'>Amount</div>
                <div className='block__money'>$475.22</div>
              </div>
              <div className='block__transfer'>
                <div className='block__title'>Fee</div>
                <div className='block__money'>$5.56</div>
              </div>
            </div>
          </div>
          <div className='transaction-info__block'>
            <div className='block__title'>status</div>
            <div className='block__content'>
              <div className='block__status'>
                <img src={checkmark} alt="checkmark" />
                <div className='block__msg'>Sent</div>
              </div>
            </div>
            <div className='block__date'>
              Sept, 14, 2021
            </div>
          </div>
          <div className='transaction-info__block'>
            <div className='block__title'>Report</div>
            <div className='block__content'>
              <div className='block__status'>
                <img src={arrowDownward} alt="arrowDownward" />
                <img src={email} alt="email" />
              </div>
            </div>
          </div>
        </div>
      </div>
  );
});

export default TransactionInfo;